<template>
<div class="fabric">
  <h5></h5>
<el-form label-width="25%">
  <div v-for="(item, index) in computedDoneFabric" :key="index">
    <!-- <el-form-item :label="labels[index]" class="bg hasbefore">
      <el-input v-model="item.name" placeholder="" class="padding-right"></el-input>
      <i  v-if="index > 0" class="el-icon-delete" @click.stop="deleteFabric(index)"></i>
    </el-form-item> -->
    <el-row :gutter="10" class="bg">
      <el-col :span="6" class="text-right"
        :class="{hasbefore : !item.name}"
        style="padding-top: 5px;padding-right: 15px">
         {{labels[index]}} 
        </el-col>
        <el-col :span="18">
      <el-input v-model="item.name" placeholder="" class="padding-right"></el-input>
      <i  v-if="index > 0" class="el-icon-delete" @click.stop="deleteFabric(index)"></i>
      </el-col>
    </el-row>

    <el-row :gutter="10"  class="space padding-bottom" >    
        <el-col :span="6" class="text-right text-style-sm"
        :class="{hasbefore : !item.picture.length}">
         : 
        </el-col> 
        <el-col :span="18">
          <Uploadfiles 
            :getUploadUrl="actionUrl"
            :defaultImg  ="image"
            :getImgList  ="item.picture"
          ></Uploadfiles>
        </el-col>
    </el-row>
      <el-form-item label=": " class="padding-right">
          <el-select v-model="item.component_id"  placeholder="" style="width:100%">
             <el-option 
              v-for="(item1, index1) in component"
              :key="'component'+index"
              :label="item1.component_name" :value="item1.component_id">
              </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label=":" class="padding-right">
        <el-input v-model="item.weight"  placeholder="" 
        ref="weight"
        @keyup.native="onlyNumber('weight', index)">  </el-input>
        <div class="after"> / </div>
      </el-form-item>
      <el-form-item label=":" class="padding-right">
        <el-input v-model="item.grammage"  placeholder="" 
        ref="grammage"
        @keyup.native="onlyNumber('grammage', index)">  </el-input>
        <div class="after"> / </div>
      </el-form-item>
      <el-form-item label=":" class="padding-right">
        <el-input v-model="item.width"  placeholder=""  style="width: 70%;"
          ref="width"
         @keyup.native="onlyNumber('width', index)">  </el-input>
        <el-radio-group v-model="item.units" style="padding-left:18px">
            <el-radio  label="" value=""></el-radio>
            <el-radio  label="" value=""></el-radio>
        </el-radio-group>
      </el-form-item>
    </div>
</el-form>
  <div class="middle-line">
      <el-button type="primary" icon="el-icon-plus" @click="addFabric" class="circle-btn"></el-button>
  </div>
</div> 
</template>

<script>
import Uploadfiles from "@/components/uploadfiles"
export default {
    // props:['receiptContent','submitReceipt'],
    components: { Uploadfiles},
    props:{
      component:{
        type: Array,
        default(){ return [] }
      },
      doneFabric: {},
      submitReceiptFabric:{
        type: Array,
        default(){ return [] }
      }
    },
    data() {
      return {
        list: [{
                name: '',
                component_id: '',
                grammage:'',
                width:'',  
                units: "", 
                weight:'',
                picture:[],
                is_main: 1
            }],
        labels: ['A'],
        startCode: 65,
        image :  require('../assets/fabric-pic.jpg'),
      }
    },
    computed:{
      actionUrl(){
        return this.Api.upload
      },
      computedDoneFabric(){
        
        if(this.doneFabric && Array.isArray(this.doneFabric) && this.doneFabric.length){
          this.list = this.doneFabric
            this.list.forEach( (item, index)=>{
              if( !item.picture ){
                item.picture = []
              }
              this.labels[index] = this.labels[index] ? this.labels[index] : "" + String.fromCharCode( this.labels.length +this.startCode )
          })
        }
        return this.list 
      }
    },
    watch:{
      list:{
        handler(curVal){
          this.$emit( 'update:submitReceiptFabric',  curVal.map( (item, index)=>{ 
                        // let obj = Object.assign({}, item)
                        let obj = this.objStringfy(item) 
                        obj.picture =  this.removeDomain( obj.picture )
                        return obj
                      })
                    )
        },
        deep: true
      }
    },
    methods: {
      onlyNumber(prop,index){
          // console.log( this.list[index][prop] )
          let str = this.list[index][prop]
          str = str.toString().replace(/[^0-9.]\D*$/,"")
          this.list[index][prop] = str
      },
      addFabric(){
        let fabric = {
              name: '',
              component: '',
              grammage:'',
              width:'',
              units: "", 
              weight:'',
              picture:[],
              is_main: 0
            }
        this.list.push(fabric)
        this.labels.push( "" + String.fromCharCode( this.labels.length +this.startCode ))
      },
      deleteFabric(index){
          this.list.splice(index,1)
          this.labels.pop()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.after{
  position: absolute;
  top: 0px;
  right: 20px;
  font-size: 1.1em;
}
.bg{
  background: rgb(204,204,204);
  padding: 20px;
  font-size: 1.1em;
  vertical-align: middle;
}
 .el-icon-delete{
  position: absolute;
  margin-left: 10px;
  right: 0;
  top: 30px;
}
.el-icon-delete:hover{
  color: #C44DDC;
}
  .show-demo1{
    border:1px solid #e0e0e0;
    height: 145px;
    width: 145px;
  }
  .floatleft{
    float: left;
    margin-right: 10px;
  }
  .hasbefore:before{
    content: '*';
    font-size: 25px;
    color: #F44336;
    font-weight: 800;
    position: absolute;
    left: 30px;
  }
</style>


